function navToggle(navContainer, toggleClass, contentContainer) {
	var $tabs = $(navContainer).find("button.tab-toggle");
	var $content = $(contentContainer);
	$content.hide();

	$.each($tabs, function () {
		if ($(this).hasClass(toggleClass)) {
			var $selector = $(this).attr("data-toggle");
			$(".module-" + $selector).show();
		}
	});
	$tabs.on("click", function (e) {
		e.preventDefault();
		$tabs.removeClass(toggleClass);
		$(this).addClass(toggleClass);
		$content.hide();
		var $selector = $(this).attr("data-toggle");
		$(".module-" + $selector).show();
	});
}
navToggle("nav", "selectedTab", ".tab-container");
